// src/utils.js

export const createSampleWrongWords = () => {
  const sampleVocabulary = `
3-1,ability,,,
3-1,abstract,,,
3-1,accountant,,,
3-2,cabinet,,,
3-2,cable,,,
3-2,cafe,,,
3-2,cafeteria,,,
3-2,cage,,,
3-3,debt,,,
3-3,decade,,,
3-3,decision,,,
3-3,decline,,,
3-3,decorations,,,
3-3,delay,,,
  `.trim();

  const wrongWords = {};

  sampleVocabulary.split('\n').forEach(line => {
    const [chapter, word, meaning] = line.split(',');
    if (!wrongWords[chapter]) {
      wrongWords[chapter] = [];
    }
    wrongWords[chapter].push({ word, meaning });
  });

  return wrongWords;
};